import CoinSwitch from "./components/CoinSwitch";
import OrderBook from "./components/OrderBook";

import useStyles from "./styles";

import {
  getPrice,
  findTokens,
  findMarketMetrics,
  findBuyBook,
  findSellBook,
  findTradesHistory
} from "../../service/SteemEngineService";

const Exchange = props => {
  const classes = useStyles();

  const [tokens, setTokens] = React.useState([]);
  const [sellBook, setSellBook] = React.useState([]);
  const [buyBook, setBuyBook] = React.useState([]);
  const [tradesHistory, setTradesHistory] = React.useState([]);

  React.useEffect(() => {
    // https://postpromoter.net/api/prices
    Promise.all([getPrice(), findTokens({}), findMarketMetrics({})]).then(
      ([_price, _tokens, _metric]) => {
        console.log({ _price, _tokens, _metric });
        const __tokens = _metric
          .map(e => {
            const {
              issuer,
              precision,
              name,
              stakingEnabled,
              supply,
              totalStaked,
              undelegationCooldown,
              unstakingCooldown,
              circulatingSupply,
              delegationEnabled,
              maxSupply,
              metadata
            } = _tokens.find(({ symbol }) => symbol === e.symbol);
            const { icon } = JSON.parse(metadata);
            return {
              symbol: e.symbol,
              highestBid: parseFloat(e.highestBid),
              lastDayPrice: parseFloat(e.lastDayPrice),
              // lastDayPriceExpiration: e.lastDayPriceExpiration,
              lastPrice: parseFloat(e.lastPrice), // 
              lowestAsk: parseFloat(e.lowestAsk),
              priceChangePercent: e.priceChangePercent, // 
              priceChangeSteem: parseFloat(e.priceChangeSteem),
              volume: parseFloat(e.volume),
              // volumeExpiration: e.symbol,
              icon,
              issuer,
              precision,
              name,
              stakingEnabled,
              supply,
              totalStaked,
              undelegationCooldown,
              unstakingCooldown,
              circulatingSupply,
              delegationEnabled,
              maxSupply
            };
          })
          .filter(e => {
            if (e.volume > 10) return true;
            return false;
          })
          .sort((a, b) => b.volume - a.volume);
        // .sort((a, b) => b.lastPrice - a.lastPrice);
        setTokens(__tokens);
      }
    );

    const opts = {
      query: { symbol: "ZZAN" }
    };
    Promise.all([
      findBuyBook(opts), //  
      findSellBook(opts), //  
      findTradesHistory(opts) //  
    ]).then(([buyBook, sellBook, tradesHistory]) => {
      console.log({ buyBook, sellBook, tradesHistory });
      const totalSell = new BigNumber(0);
      const _sellBook = sellBook
        .map(e => {
          const total = new BigNumber(e.quantity).times(e.price).toNumber();
          return {
            // account: e.account,
            // expiration: e.expiration,
            price: parseFloat(e.price),
            quantity: parseFloat(e.quantity),
            symbol: e.symbol,
            // timestamp: e.timestamp,
            // txId: e.txId,
            total
          };
        })
        .sort((a, b) => b.price - a.price);
      const _buyBook = buyBook
        .map(e => {
          const total = new BigNumber(e.quantity).times(e.price).toNumber();
          return {
            // account: e.account,
            // expiration: e.expiration,
            price: parseFloat(e.price),
            quantity: parseFloat(e.quantity),
            // symbol: e.symbol,
            // timestamp: e.timestamp,
            // txId: e.txId,
            total
          };
        })
        .sort((a, b) => b.price - a.price);
      const _tradesHistory = tradesHistory
        .map(e => {
          return {
            // account: e.account,
            type: e.type,
            symbol: e.symbol,
            price: parseFloat(e.price),
            quantity: parseFloat(e.quantity),
            timestamp: e.timestamp,
            volume: e.volume
          };
        })
        .sort((a, b) => b.price - a.price);
      setSellBook(_sellBook);
      setBuyBook(_buyBook);
      setTradesHistory(_tradesHistory);
    });
  }, []);

  return (
    <React.Fragment>
      <div className={classNames("page-global-exchange", "page")}>
        <div className="content-top">
          <div className="left">
            <CoinSwitch tokens={tokens} {...props} />
          </div>
          <div className="right">
            <div className="l" />
            <div className={classNames("r", "trades-container")}>
              <div className="global-trades">
                <ul>
                  <li className={classNames({ cur: true })}></li>
                  <li className={classNames({ cur: false })}></li>
                </ul>
                <div>
                  <OrderBook sellBook={sellBook} buyBook={buyBook} />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Exchange;
